(function(module) {
try {
  module = angular.module('ui.bootstrap.modal');
} catch (e) {
  module = angular.module('ui.bootstrap.modal', []);
}
module.run(['$templateCache', function($templateCache) {
  $templateCache.put('template/modal/confirm/confirm.html',
    '<div class="modal-header">\n' +
    '	<h2 class="modal-title">\n' +
    '		{{ ::$ctrl.options.title }}\n' +
    '	</h2>\n' +
    '</div>\n' +
    '\n' +
    '<div class="modal-body">\n' +
    '	<p>{{ ::$ctrl.options.message }}</p>\n' +
    '</div>\n' +
    '\n' +
    '<div class="modal-footer">\n' +
    '	<button class="btn btn-success-outline" ng-click="$ctrl.ok()" ng-switch="::$ctrl.options.buttonType">\n' +
    '		<span class="jolticon jolticon-check"></span>\n' +
    '		<span ng-switch-when="ok" translate>OK</span>\n' +
    '		<span ng-switch-when="yes" translate>Yes</span>\n' +
    '	</button>\n' +
    '	<button class="btn btn-danger-outline" ng-click="$ctrl.cancel()" ng-switch="::$ctrl.options.buttonType">\n' +
    '		<span class="jolticon jolticon-remove"></span>\n' +
    '		<span ng-switch-when="ok" translate>Cancel</span>\n' +
    '		<span ng-switch-when="yes" translate>No</span>\n' +
    '	</button>\n' +
    '</div>\n' +
    '');
}]);
})();
